		.running-strip-container {
			overflow-x: hidden;
			position: fixed;
			top: 10vh;
			left: 0;
			width: 100%;
			text-align: center;
			
		  }
		  
		  .running-strip {
			display: inline-block;
			white-space: nowrap;
			animation: marquee 20s linear infinite;
			transform: translateX(100%);
		  }
		  
		  .running-strip p {
			color: white;
		  }
		  
		  @keyframes marquee {
			0% {
			  transform: translateX(100%);
			}
			100% {
			  transform: translateX(-100%);
			}
		  }
		  .visible {
			display: block;
		  }
		  
		  .hidden {
			display: none;
		  }
		  .counter-container {
			display: flex;
			justify-content: center;
			align-items: center;
			height: 40vh;
			margin-top: 8vh;
		  }
		  
		  .inner {
			text-align: center;
		  }
		  
		  .inner h1,
		  .inner h2,
		  .time-label,
		  .inner p {
			color: white;
		  }
		  
		  .inner p {
			color: white;
			font-size: 30px;
			margin-top: 10px;
		  }
		  
		  .timer {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 20px 0;
		  }
		  
		  .time {
			font-size: 4rem;
			margin: 0 10px;
			background: #222;
			color: white;
			border-radius: 5px;
			padding: 10px;
		  }
		  
		  @media screen and (max-width: 768px) {
			.time {
			  font-size: 3rem;
			  padding: 8px;
			}
			.inner p {
			  font-size: 20px;
			}
		  }
		  
		  @media screen and (max-width: 480px) {
			.time {
			  font-size: 2.5rem;
			  padding: 6px;
			}
			.inner p {
			  font-size: 16px;
			}
		  }

		  @media screen and (max-width: 360px) {
			.time {
			  font-size: 2rem;
			  padding: 4px;
			}
			.inner p {
			  font-size: 12px;
			}
		  }
		  
		  @media screen and (max-width: 320px) {
			.time {
			  font-size: 1.5rem;
			  padding: 4px;
			}
			.inner p {
			  font-size: 10px;
			}
		  }
		  